
.Timer {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    width: 90vw;

    animation-name: slideUp;
    animation-duration: 500ms;
    opacity: 1;

    z-index: 20;
}

@keyframes slideUp {
    from {
        bottom: -20px;
        opacity: 0;
    }    
}
